<template>
  <v-container fluid>
    <v-layout row>
      <v-flex md12 class="py-3 grey lighten-3 text-xs-center">
        <div class="headline">Data User</div>
        <div class="subtitle">Menampilkan pengguna yang bisa masuk dan melakukan perubahan dalam sistem.</div>
        <v-btn
          dark
          large
          color="blue"
          depressed
          @click="$router.push('/app/add-user')"
        >Tambah</v-btn>
      </v-flex>
    </v-layout>
    <v-data-table
      :headers="tableHeaders"
      :items="tableItems"
      :total-items="tableItems.length"
    >
      <template v-slot:items="props">
        <td>{{ props.item.name }}</td>
        <td>{{ props.item.username }}</td>
        <td>
          <v-btn :to="`/app/update-user/${props.item._id}`" dark small depressed color="blue">edit</v-btn>
          <v-btn @click="deleteUser(props.item._id)" dark small depressed color="pink">delete</v-btn>
        </td>
      </template>
    </v-data-table>
  </v-container>
</template>

<script>
import evbus from "@/evbus";
import { User__find, User__delete, User__findOne } from "@/services/users";
import tableHeaders from "./tableHeaders";

export default {
  data: () => ({
    tableHeaders,
    tableItems: []
  }),
  methods: {
    async loadUsers() {
      evbus.$emit("loading-on");
      try {
        const items = await User__find();
        this.tableItems = items;
        evbus.$emit("success", "Sukses memuat data user");
      } catch (err) {
        console.log(err);
        evbus.$emit("error", "Gagal memuat data user");
      } finally {
        evbus.$emit("loading-off");
      }
    },
    async deleteUser(id) {
      evbus.$emit("loading-on");
      try {
        await User__delete(id);
        evbus.$emit("success", "Sukses menghapus data user");
      } catch (err) {
        evbus.$emit("success", "Gagal menghapus data user");
        console.log(err);
      } finally {
        evbus.$emit("loading-on");
        this.loadUsers();
      }
    }
  },
  mounted() {
    this.loadUsers();
  }
}
</script>

