import axios from "./axios";

export async function User__find() {
  const response = await axios.get("/users");
  return response.data;
}

export async function User__findOne(id: string) {
  const response = await axios.get(`/users/${id}`);
  return response.data;
}

export async function User__create(payload: any) {
  const response = await axios.post("/users", payload);
  return response.data;
}

export async function User__update(id: string, payload: any) {
  let data = { ...payload };
  delete data._id;
  const response = await axios.put(`/users/${id}`, data);
  return response;
}

export async function User__delete(id: string) {
  await axios.delete(`/users/${id}`);
}

